/* eslint-disable */
import React from 'react';
import { Link } from 'react-router-dom';
import styles from './Header.module.css';
import styled from 'styled-components';
import { useEffect, useState } from 'react';
import { throttle } from 'lodash';
import ModeChanger from './ModeChanger';
import { useRecoilValue } from 'recoil';
import { cartsListAtom } from '../recoil/cartsList';

function Header( props ) {
    // 스크롤에 따른 <Header /> 변환을 위한 코드 Start
    const [scrollPosition, setScrollPosition] = useState(0); // scrollPosition을 변화시킬 state로 정의.
    
    const updateScroll = () =>{
        setScrollPosition(window.scrollY || document.documentElement.scrollTop); // Y좌표값을 구해주는 함수.
    }

    const throttleScroll = throttle(updateScroll, 100); // updateScroll 함수를 1초에 한번씩 실행시켜주는 함수. Lodash 라이브러리 이용.

    useEffect( () => {
        window.addEventListener('scroll', throttleScroll); // scroll이 되면 handlesScroll 함수를 실행시킨다.
        return () => {
            window.removeEventListener('scroll', throttleScroll); // clean up.
        }
    }, []);

    // console.log(scrollPosition);

    const[visible, setVisible] = useState(false);    
    useEffect( () => {                      // 위에서 구한 Y좌표를 useEffect를 이용하여 실시간으로 비교한다.
        if( scrollPosition < 100){
            setVisible(false);
        }
        else { setVisible(true); }
    })

    const checker = visible ? 'smallHeader' : 'hidden';
    // 스크롤에 따른 <Header /> 변환을 위한 코드 End


    const[isHover, setIsHover] = useState(false);

    const cartsList = useRecoilValue(cartsListAtom);
    // console.log(cartsList);
    // console.log(cartsList.length);

    return (
        <div className={styles.header}>
            <div className={styles.normalHeaderInner}>
                <div className={scrollPosition < 100 ? 'styles.normalHeader' : 'stlyes.hidden'}>
                    <div className={styles.top}>
                        <Link to="SignUp">회원가입</Link>
                        <Bar />
                        <Link to="SignIn">로그인</Link>
                        <Bar />
                        <Link to="/">고객센터<span className={styles.underImg} /></Link>
                    </div>
                    <div className={styles.middle}>
                        <Link to="/">
                            <div className={styles.logoContainer}>
                                <img src='data:image/svg+xml;base64,' alt="로고 이미지" />
                            </div>
                        </Link>
                        <ModeChanger />
                        <div className={styles.searchBar}>
                            <input type="text" className={styles.searchInput} placeholder='검색어를 입력해주세요.'/>
                            <button className={styles.searchButton}></button>
                        </div>
                        <div className={styles.userBox}>
                            <div className={styles.buttonContainer}>
                                <button className={styles.mapButton}></button>
                            </div>
                            <button className={styles.likeButton}></button>
                            <div className={styles.buttonContainer}>
                                <Link to='Cart'>
                                    <button className={styles.cartButton}>
                                        { cartsList.length >= 1 ? (
                                            <span className={styles.cartCount}> {cartsList.length} </span>
                                        ) : (
                                            ""
                                        )}
                                    </button>
                                </Link>
                            </div>
                        </div>
                    </div>
                    <div className={styles.bottom}>
                        <div className={styles.category}
                            onMouseOver = { () => {setIsHover(true);}}
                            onMouseOut = { () => {setIsHover(false);}}
                        >
                            <img className={styles.categoryImg} src={ isHover ? 'data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTYiIGhlaWdodD0iMTQiIHZpZXdCb3g9IjAgMCAxNiAxNCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICAgIDxwYXRoIGQ9Ik0wIDBoMTZ2MS43SDBWMHptMCA2LjE1aDE2djEuN0gwdi0xLjd6bTAgNi4xNWgxNlYxNEgwdi0xLjd6IiBmaWxsPSIjNUYwMDgwIiBmaWxsLXJ1bGU9ImV2ZW5vZGQiLz4KPC9zdmc+Cg==' : 'data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTYiIGhlaWdodD0iMTQiIHZpZXdCb3g9IjAgMCAxNiAxNCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICAgIDxwYXRoIGQ9Ik0wIDBoMTZ2MS43SDBWMHptMCA2LjE1aDE2djEuN0gwdi0xLjd6bTAgNi4xNWgxNlYxNEgwdi0xLjd6IiBmaWxsPSIjMzMzIiBmaWxsLXJ1bGU9ImV2ZW5vZGQiLz4KPC9zdmc+Cg=='} alt='카테고리 이미지' />
                            <span className={styles.categoryText}>카테고리</span>
                        </div>
                        <ul className={styles.navMenu}>
                            <li className={styles.menuList}><span>신상품</span></li>
                            <li className={styles.menuList}><span>베스트</span></li>
                            <li className={styles.menuList}><span>알뜰쇼핑</span></li>
                            <li className={styles.menuList}><span>특가/혜택</span></li>
                        </ul>
                        <div className={styles.delivery}>
                            <a href='' className={styles.deliveryTag}>
                                <span>샛별・낮</span>배송안내
                            </a>
                        </div>
                    </div>
                </div>
                {/* <div className={`${styles[scrollPosition > 100 ? 'smallHeader' : 'hidden']}`}> */}
                <div className={`${styles[checker]}`}>
                    <div className={styles.smallHeaderInner}>
                        <div className={styles.category}
                            onMouseOver = { () => {setIsHover(true);}}
                            onMouseOut = { () => {setIsHover(false);}}
                        >
                            <img className={styles.categoryImg} src={ isHover ? 'data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTYiIGhlaWdodD0iMTQiIHZpZXdCb3g9IjAgMCAxNiAxNCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICAgIDxwYXRoIGQ9Ik0wIDBoMTZ2MS43SDBWMHptMCA2LjE1aDE2djEuN0gwdi0xLjd6bTAgNi4xNWgxNlYxNEgwdi0xLjd6IiBmaWxsPSIjNUYwMDgwIiBmaWxsLXJ1bGU9ImV2ZW5vZGQiLz4KPC9zdmc+Cg==' : 'data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTYiIGhlaWdodD0iMTQiIHZpZXdCb3g9IjAgMCAxNiAxNCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICAgIDxwYXRoIGQ9Ik0wIDBoMTZ2MS43SDBWMHptMCA2LjE1aDE2djEuN0gwdi0xLjd6bTAgNi4xNWgxNlYxNEgwdi0xLjd6IiBmaWxsPSIjMzMzIiBmaWxsLXJ1bGU9ImV2ZW5vZGQiLz4KPC9zdmc+Cg=='} alt='카테고리 이미지' />
                            <span className={styles.categoryText}>카테고리</span>
                        </div>
                        <ul className={styles.navMenu}>
                            <li className={styles.menuList}><span>신상품</span></li>
                            <li className={styles.menuList}><span>베스트</span></li>
                            <li className={styles.menuList}><span>알뜰쇼핑</span></li>
                            <li className={styles.menuList}><span>특가/혜택</span></li>
                        </ul>
                        <div className={styles.smallSearchBar}>
                            <input type="text" className={styles.searchInput} placeholder='검색어를 입력해주세요.'/>
                            <button className={styles.smallSearchButton}></button>
                        </div>
                        <div className={styles.userBox}>
                            <div className={styles.buttonContainer}>
                                <button className={styles.mapButton}></button>
                            </div>
                            <button className={styles.likeButton}></button>
                            <div className={styles.buttonContainer}>  
                                <Link to='Cart'>                            
                                    <button className={styles.cartButton}>
                                        { cartsList.length >= 1 ? (
                                            <span className={styles.cartCount}> {cartsList.length} </span>
                                        ) : (
                                                ""
                                        )}
                                    </button>
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

const Bar = styled.div`
    width: 1px;
    height: 13px;
    margin: 0px 12px;
    background-color: rgb(217, 217, 217);
`

export default Header;