/* eslint-disable */
import React from 'react';
import styles from './MobileHeader.module.css';
import { Link } from 'react-router-dom';
import ModeChanger from './ModeChanger';

function MobileHeader(props) {
    return (
        <div className={styles.MobileHeader}>
            <div className={styles.top}>
                <div className={styles.inner}>
                    <Link to="/">                        
                        <img src='data:image/svg+xml;base64,' alt="로고 이미지" />
                    </Link>
                    <ModeChanger />
                    <div className={styles.buttonContainer}>
                        <div className={styles.mapButton} />
                        <div className={styles.cartButton}></div>
                    </div>
                </div>
            </div>
            <div className={styles.bottom}>
                <div className={styles.inner}>
                    <div className={styles.Nav}>
                        <div className={styles.NavList}><span>컬리추천</span></div>
                        <div className={styles.NavList}><span>신상품</span></div>
                        <div className={styles.NavList}><span>베스트</span></div>
                        <div className={styles.NavList}><span>알뜰쇼핑</span></div>
                        <div className={styles.NavList}><span>특가/혜택</span></div>
                    </div>
                </div>
             </div>
        </div>
    );
}

export default MobileHeader;